body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.headerContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    width: 100%;
}

.headerGroupItem {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.headerInnerItem {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.currentBlockNumberContainer {
    height: 100%;
}

.currentBlockNumber {
    font-size: 1.8em;
    padding: 8px;
    background-image: linear-gradient(130deg,#00B4E6,#00F0E0);
    border-radius: 3px;
}

.timeAgo {
    font-size: 1.4em;
}
.TransactionInfoContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 10px;
}

.transactionText {
    font-size: 1.2em;
}

.transactionInnerContainer {
    display: flex;
    flex-direction: column;
}
.BlockListItem {
    width: 75vw;
    margin-bottom: 10px;
}

.itemBlockHash {
    font-size: 1em;
}

.tableCellContent {
    padding: 10px;
    display: flex;
    flex-direction: column;
}

.etherscanButton {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    text-align: center;
    padding: 20px;
}

.rightButton {
    margin-right: 5vw;
}
.blockList {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
}
.App {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
  min-height: 100vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Header {
  margin-bottom: 6vh;
}

.aragonCard {
  box-shadow: rgba(0, 0, 0, 0.07) 0px 4px 6px 0px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;
  width: 75vw;
  border-radius: 3px;
  padding: 30px;
  transition: all 0.25s ease-in-out 0s;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

